.boton-clear{
    height: 80px;
    font-size: 1.6em;
    display: flex;
    flex: 1;
    background-color: crimson;
    margin-top: 8px;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    color: aliceblue;
    border: 2px solid white;
    border-radius: 45px;
    cursor: pointer;
}

.boton-clear:hover{
    background-color: rgb(211, 69, 97);
}